.statusCard {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;

  img {
    width: 25%;
  }
  .statWrapper {
    color: white;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 5px;

    > h2 {
      font-weight: 700;
      font-size: 16px;
    }
    > p {
      font-weight: 900;
      font-size: 24px;
      margin-bottom: 0;
    }
  }
}

@media (max-width: 765px) {
  .statusCard {
    flex-direction: column;
    img {
      width: 35%;
    }
    .statWrapper {
      text-align: center;
      justify-content: center;

      > h2 {
        font-weight: 700;
        font-size: 14px;
      }
      > p {
        font-weight: 900;
        font-size: 16px;
        line-height: 18px;
        margin-bottom: 0;
      }
    }
  }
}
