@use "../../variables" as var;
.WalletMenu {
  position: relative;
  width: fit-content;
  user-select: none;
}
.walletList {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  .walletItem {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    background-color: rgba(#2a1f68, 0.5);
    padding: 10px;
    border-radius: 10px;

    cursor: pointer;
    img {
      width: 45px;
    }
    p {
      margin: 0;
      color: var.$color-content-text;
      font-size: 22px;
      font-weight: 700;
    }
    &:hover {
      opacity: 0.8;
    }
  }
}
