@use "../../../variables" as var;

.CustomModal {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background-color: rgba(#070b2d, 1);
    border-radius: 24px;
    padding: 0;
  }
  :global(.ant-modal-body) {
    padding: 20px;
    color: #ffffff;
  }
}

.titleWrap {
  position: relative;
  display: flex;
  margin-bottom: 10px;
  // flex-direction: column;
  // align-items: flex-start;
  img {
    width: 20px;
    filter: invert(30%) sepia(83%) saturate(2414%) hue-rotate(226deg)
      brightness(101%) contrast(104%);
    cursor: pointer;
  }
  h2 {
    width: 100%;
    text-align: center;
    color: var.$color-content-text;
    font-size: 24px;
    font-weight: 700;
    margin: 0;
    text-align: left;
  }
}
