.nftCard {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #060a20;
  border-radius: 10px;

  .imgWrapper {
    background: #0a0f29;
    display: flex;
    justify-content: center;
    border-radius: 10px 10px 0 0;
    > img {
      width: 100%;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;

    .apy {
      background: linear-gradient(91.29deg, #ff48f8 3.97%, #5928fd 99.29%);
      border-radius: 20px;
      padding: 15px 20px;
      width: fit-content;
      margin: 10px auto;

      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      color: white;
    }

    .details {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 10px;
      > p {
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        color: white;
        margin-bottom: 0;
      }
    }
  }
}
