@use "../../variables" as var;
.About {
  position: relative;
  width: 100%;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  overflow-x: hidden;
}
.themeImageWrap {
  margin-top: 40px;
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  // height: 90vh;
  img {
    width: 95%;
    max-width: 1400px;
  }
  .logo {
    width: 20%;
    min-width: 250px;
    max-width: 400px;
  }
}
.mainContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 50px;
  padding-top: 50px;
}

.introWrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  // .text {
  //   width: 50%;
  // }
  img {
    width: 350px;
  }
}
.featureWrap {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 20px;
  .text {
  }
  img {
    width: 300px;
  }
}
.supportWrap {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: space-between;
  gap: 35px;
  .text {
    width: 40%;

    h2 {
      margin: 0;
    }
  }
  .supportList {
    position: relative;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .supportListRow {
      position: relative;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      transform: translateY(calc(var(--n) * 25% * -1));
      .supportListItem {
        width: 25%;
        img {
          width: 100%;
        }
      }
    }
  }
}

.text {
  text-align: left;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 10px;
  ul {
    list-style: none; /* Remove default bullets */
    padding: 0;
  }
  li {
    margin-bottom: 5px;
  }

  ul li::before {
    content: "\2022"; /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: var.$color-content-text; /* Change the color */
    font-weight: bold; /* If you want it to be bold */
    display: inline-block; /* Needed to add space between the bullet and the text */
    width: 0.5em; /* Also needed for space (tweak if needed) */
    // margin-left: -1em; /* Also needed for space (tweak if needed) */
  }
}

@media (max-width: 765px) {
  .themeImageWrap {
    height: unset;
    img {
      width: 140%;
    }
  }
  .mainContent {
    padding-top: 0;
  }

  .introWrap {
    flex-direction: column;
    justify-content: flex-start;
  }
  .featureWrap {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    img {
      width: 300px;
    }
  }
  .referralWrap {
    .titleWrap {
      flex-direction: column;
      align-items: center;
      gap: 10px;
      .title {
        align-items: center;
      }
    }
  }

  .supportWrap {
    flex-direction: column;

    .imageWrap,
    .text {
      width: 80%;
    }
  }
}
